import React, { useState } from "react";
import { motion } from "framer-motion";
import Countdown from "react-countdown";
import weddingImage from "./assets/b-g-image.png";
import fallbackImage from "./assets/image.png";
import "./WeddingCard.css";

const WeddingCard = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [isVideoLoaded, setIsVideoLoaded] = useState(false);

  const weddingDate = new Date("December 14, 2024 17:00:00");

  // Handle card flip manually
  const toggleCard = () => {
    setIsOpen((prev) => !prev);
  };

  // Countdown renderer
  const renderCountdown = ({ days, hours, minutes, seconds }) => (
    <span>
      {days} Days {hours}:{minutes}:{seconds}
    </span>
  );

  return (
    <div className="card-container" onClick={toggleCard}>
      <motion.div
        className="card"
        animate={{ rotateY: isOpen ? 180 : 0 }}
        transition={{ duration: 0.8, ease: "easeOut" }}
      >
        {/* Front Content */}
        {!isOpen && (
          <div className="video-container">
            <video
              src="https://maheshnd.github.io/wedding-invitation/wedding-video.mp4"
              autoPlay
              muted={false}
              playsInline
              loop={false} // Disable looping
              onCanPlayThrough={() => setIsVideoLoaded(true)}
              onEnded={() => setIsOpen(true)} // Flip the card when video ends
              className={`video-frame ${isVideoLoaded ? "visible" : "hidden"}`}
            />
            {!isVideoLoaded && (
              <img
                src={fallbackImage}
                alt="Fallback Wedding Background"
                className="fallback-image"
              />
            )}
          </div>
        )}

        {/* Back Content */}
        {isOpen && (
          <motion.div className="card-back">
            <motion.div>
              <img
                src={weddingImage}
                alt="Wedding"
                className="background-image"
              />
            </motion.div>
            <h1 className="back-title">Join Us!</h1>
            <div className="event-info">
              <div className="event-item">
                <div>
                  <strong>Haldi</strong>
                  <p>13th December 2024 at 6:30 PM</p>
                </div>
              </div>
              <div className="event-item">
                <div>
                  <strong>Wedding</strong>
                  <p>14th December 2024 at 12:29 PM</p>
                </div>
              </div>
            </div>
            <motion.div className="map-container">
              <h3 className="map-title">
                <span className="map-icon">📍</span>Yash Palace, Yeola
              </h3>
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d15186.571662722168!2d74.4793791!3d20.0570229!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bdc39e7f6980c27%3A0x3397ceb311ea1c6b!2sYash%20Palace!5e1!3m2!1sen!2sin!4v1733594333702!5m2!1sen!2sin"
                width="700"
                height="550"
                className="map"
                allowFullScreen=""
                loading="lazy"
                title="Wedding Location Map"
                referrerPolicy="no-referrer-when-downgrade"
              ></iframe>
            </motion.div>
          </motion.div>
        )}

        {/* Timer Overlay */}
        {!isOpen && (
          <motion.div className="timer-overlay">
            <h2 className="timer">
              <Countdown date={weddingDate} renderer={renderCountdown} />
            </h2>
          </motion.div>
        )}
      </motion.div>
    </div>
  );
};

export default WeddingCard;
